@media only screen and (max-width: 1600px) {
  #preloader {

  }
  #header {
    .group ul {
      a {
        padding: 1rem 1.5rem;
      }
    }
    .btn-default {
      margin-left: 7rem;

      a {

        &:after {
          left: -30%;
          width: 50%;
        }
        &:hover:after {
          transform: scale(.6, 1);
        }
      }
    }
  }
  #fullpage {
    overflow-x: hidden;

    .section-reviews {

      .reviews-content {
        .reviews-video {
          width: 700px;

          &:after {
            top: 2rem;
            left: -3rem;
          }
        }

        .reviews-text {
          margin-bottom: 0;

          h3 {
            left: 0;
            font-size: 10rem;
          }
        }
      }
      .reviews-slider .fp-tableCell {
        align-items: flex-start;
      }
    }
    .section-news {

      .news-content {
        .news-video {

          &:after {
            top: 2rem;
            left: -3rem;
          }
        }

        .news-text {
          margin-bottom: 0;
          max-width: 600px;
        }
      }
      .news-slider .fp-tableCell {
        align-items: flex-start;
      }
    }
  }
  #footer {

  }
}


