@media only screen and (max-width: 1199px) {
  #preloader {

  }
  #header {
    &:before {
      content: '';
      position: absolute;
      pointer-events: none;
      opacity: 0;
      width: 100%;
      height: 0;
      left: 0;
      top: 0;
      z-index: 0;
      box-shadow: 0 -40px 100px 100px rgba(49,69,82,.8);
      transition: all .5s ease;
    }
    &.scroll:before {
      opacity: 1;
      transition: all .5s ease;
    }
  }
  #fullpage {
    .section {

      .container-fluid {


        .mission-content {
          margin-top: 5rem;
          margin-bottom: 3rem;
          overflow: hidden;

          h2 {
            margin-bottom: 0;
            margin-right: 0;
            right: -35px;

            span:first-child {
              line-height: 7rem;
            }

            &:after {
              top: 42px;
              left: -30%;
            }
          }
        }
        .mission-image {
          margin-bottom: 5rem;
        }

        .col-xl-5 > .about-content:first-child {
            margin-bottom: 5rem;
        }

        .product-title {
          h2 {
            flex-flow: column;
            font-size: 6rem;

            span:first-child {
              line-height: 7rem;
            }
            span:first-child span {
              font-size: inherit;
            }

            &:after {
              top: inherit !important;
              bottom: 20%;
              right: -7rem;
            }
          }
        }
        .product-content {
          &:after {
            display: none !important;
          }
        }

      }
    }
    .section-cases .fp-slides {
      .fp-tableCell {
        height: auto !important;
      }
      .wrap-slider {
        height: auto;

        img {
          max-width: 100%;
          width: 350px;
        }
      }
    }
    .fp-controlArrow.fp-prev {
      left: 0;
    }

    .section-contact {
      .col-xl-6.d-flex.justify-content-center {
        justify-content: flex-start !important;
        margin-top: 3rem;
      }
      .fp-bg:after {
        width: 100%;
      }
      .col-xl-7 {
        margin-top: 2rem;
        margin-bottom: 5rem;
      }
      h3 {
        margin-bottom: 2rem;
      }
    }
    .section-news {
      .fp-slides {
        overflow: hidden !important;
      }
      .fp-tableCell {
        flex-flow: column;
        padding-top: 3rem;
      }
      .col-xl-6 {
        flex: 0 0 100%;
        max-width: 100%;
        justify-content: center !important;
      }

      .news-content {
        .news-video {
          width: 600px;
        }
        .news-text {
          max-width: 650px;
          max-height: 310px;
          margin-top: 0;
          margin-bottom: 0;

          h3 {
            //left: auto;
            font-size: 3rem;
            margin-top: 4rem;
            margin-bottom: 0;
          }
        }
      }
      //.col-xl-5 {
      //  .news-content {
      //
      //    .news-text {
      //      display: flex;
      //      flex-flow: row;
      //      align-items: center;
      //      max-width: 650px;
      //      margin-top: 0;
      //      margin-bottom: 0;
      //
      //      h3 {
      //        left: auto;
      //        font-size: 15rem;
      //      }
      //    }
      //  }
      //}
    }
  }
  .modal-custom {
    .modal-head {
      justify-content: space-between;
      h2 {
        width: 100%;
        text-align: center;
      }
      .btn-close {
        position: relative;
      }
    }
  }
  #footer {
    &:before {
      content: '';
      position: absolute;
      pointer-events: none;
      opacity: 1;
      width: 100%;
      height: 0;
      left: 0;
      top: 0;
      z-index: 0;
      box-shadow: 0 70px 50px 100px rgba(49,69,82,.7);
      transition: all .5s ease;
    }
    &.scroll:before {
      opacity: 1;
      transition: all .5s ease;
    }
    .footer-info {
      width: 500px;
    }
  }
}


